<script>
import { MessageBox, Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import dayjs from 'dayjs';
import {
    GetBusinessRenewalContractByOrderRoom,
    PostBusinessOrderroomSendMessageAgain,
    PostBusinessOrderroomLeave,
    PostBusinessOrderroomPasswordSend,
    PostBusinessOrderroomCancel,
    PostBusinessOrderroomuserRemove,
    PostBusinessOrderroomDelaySwitchOn,
    PostBusinessOrderroomAutoDetainOn,
    PostBusinessOrderroomAutoDetainOff,
    PostBusinessOrderroomNocardOn,
    GetBusinessOrderroomuserByorderroom,
    GetBusinessDepositCutRecordByorderroom,
    GetBusinessOrderroom,
    PostBusinessOrderroomRoomchange,
    GetBusinessRoomscaleDiscountInfo,
    PostBusinessUserCouponAdd
} from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperOrderroom, GetBusinessCooperRenewalContractByCooperOrder, GetBusinessRoomFreeCooperByhotel } from '@/api/api_business';
export default {
    data() {
        return {
            status: [1, 2],
            payment: '',
            order_add: {
                1: '哎哟有房',
                2: '订单同步',
                3: '手动分房',
                4: '短信接单'
            },
            a: '',
            value: '',
            pass_phone: '',
            currentRole: '',
            number: '',
            dictLabel: '',
            currentInfo: null,
            getLocalInfo: null,
            roomPayments: null,
            orderPayMent: {},
            discountInfo: '',
            depositStatu: '',
            roomStatus: [],
            cutRecordInfo: [],
            infoList: [],
            payments: [],
            signingInfo: [],
            patmentObj: [],
            priceRecordList: [],
            roles: [],
            cooperChangeRooms: [],
            paymentList: [],
            orderSourceMap: [],
            ordertTypeAll: [],
            selectedData: [],
            coupons: [],
            priceRecordListBackInfo: [],
            next_day: null,
            orderStatusAll: null,
            show: false,
            on: false,
            disabled: false,
            disabled1: false,
            disabled2: false,
            showleave: false, //提前退房
            sheetVisible: false,
            isLock: false,
            edit_box: false,
            person_box: false,
            deposit: false,
            password_show: false,
            turnOpen: false,
            Signing: false,
            renting: false,
            cooperRooms: false,
            showCoupon: false,
            showTime: false,
            ROLE_MANAGER: false,
            ROLE_COOPER_MANAGER: false,
            memName: '',
            memNumber: '',
            menPrice: '',
            date: '选择有效时间段',
            delayValue: ''
        };
    },
    async created() {
        this.$store.commit('setHeader', '二房东订单详情');
        let login_ok_info = JSON.parse(localStorage.getItem('current_user'));
        if (login_ok_info.id == '1') {
            this.ROLE_MANAGER = true;
            this.ROLE_COOPER_MANAGER = true;
        } else {
            this.roles = JSON.parse(localStorage.getItem('roles'));
            this.roles.some((item) => {
                if (item.name == 'ROLE_COOPER_MANAGER') {
                    return (this.ROLE_COOPER_MANAGER = true);
                } else {
                    return (this.ROLE_COOPER_MANAGER = false);
                }
            });
            this.roles.some((item) => {
                if (item.name == 'ROLE_MANAGER') {
                    return (this.ROLE_MANAGER = true);
                } else {
                    return (this.ROLE_MANAGER = false);
                }
            });
        }
        this.orderRoomId = this.$route.query.orderRoomId;
        await this.getUnitStatu({ dictType: 'business_payment_method' }, 'roomPayments');
        if (this.$route.query.orderRoomId) {
            await this.getUnitStatu({ dictType: 'business_orderroom_status' }, 'roomStatus');
            this.getCurrentInfo();
        } else {
            await this.getUnitStatu({ dictType: 'business_cooper_order_status' }, 'roomStatus');
            this.getCooperCurrentInfo();
        }
        let cTime = new Date();
        let Y = cTime.getFullYear();
        let M = cTime.getMonth() + 1;
        let D = cTime.getDate();
        let H = cTime.getHours();
        let Mm = cTime.getMinutes();
        let S = cTime.getSeconds();
        if (M < 10) {
            M = '0' + M;
        } else {
            M = M;
        }

        if (D < 10) {
            D = '0' + D;
        } else {
            D = D;
        }

        if (H < 10) {
            H = '0' + H;
        } else {
            H = H;
        }

        if (Mm < 10) {
            Mm = '0' + Mm;
        } else {
            Mm = Mm;
        }

        if (S < 10) {
            S = '0' + S;
        } else {
            S = S;
        }
        this.a = Y + '-' + M + '-' + D + ' ' + H + ':' + Mm + ':' + S;
        this.a = dayjs(this.a).unix();
    },
    computed: {
        getStatus: {
            get() {
                if (this.currentInfo) {
                    return this.roomStatus.find((item) => item.dictValue == this.currentInfo.status).dictLabel;
                }
            }
        }
    },
    methods: {
        // 拨号
        callPhone(phoneNumber) {
            window.location.href = 'tel://' + phoneNumber;
        },
        // 根据id获取单位订单
        getCooperCurrentInfo() {
            GetBusinessCooperOrderroom({ cooperOrderRoomId: this.$route.query.cooperOrderRoomId }).done((res) => {
                this.getCooperSigning();
                this.currentInfo = res.data;
            });
        },
        async getUnitStatu(obj, data) {
            await GetSystemDictDataByType(obj).done((res) => {
                this[data] = res.data;
            });
        },
        // 根据单位订单查询合同
        getCooperSigning() {
            GetBusinessCooperRenewalContractByCooperOrder({ cooperOrderId: this.$route.query.cooperOrderRoomId }).done((res) => {
                this.firstGo();
                let list = res.data.map((item) => {
                    this.$set(item, 'setStatu', false);
                    return item;
                });
                this.signingInfo = list;
            });
        },
        // 时间排序
        sortKey(array, key) {
            return array.sort(function (a, b) {
                var x = a[key];
                var y = b[key];
                return x > y ? -1 : x < y ? 1 : 0;
            });
        },
        // 当前信息
        getCurrentInfo() {
            GetBusinessOrderroom(this.orderRoomId).done((res) => {
                localStorage.setItem('moreInfo', JSON.stringify(res.data));
                this.currentInfo = res.data;
                if (this.currentInfo.orderType != 3) {
                    this.getOrderType('status', 'business_payment');
                }
                this.firstGo();
                if (this.currentInfo.userId > 0) {
                }
                this.next_day = dayjs(dayjs(this.currentInfo.order.startTime).add(1, 'day').format('YYYY-MM-DD') + ' ' + '08:00:00').unix();
                if (this.currentInfo.status == 4 || this.currentInfo.status == 7 || this.currentInfo.orderType == 1 || this.currentInfo.orderType == 2) {
                    // 退房时间和订单开始时间在同一天 不超过退房时间2个夜审日 退房时间和订单开始时间不在同一天 不超过退房时间1个夜审日
                    let cunrrentTime = dayjs().unix();
                    if (this.currentInfo.leaveTime && this.currentInfo.order.startTime) {
                        let diffDay = dayjs(this.currentInfo.leaveTime.substr(0, 10)).diff(dayjs(this.currentInfo.order.startTime.substr(0, 10)), 'day');
                        let go_next_day = '';
                        if (diffDay == 0) {
                            go_next_day = dayjs(dayjs(this.currentInfo.leaveTime).add(2, 'day').format('YYYY-MM-DD') + ' ' + '08:00:00').unix();
                        } else {
                            go_next_day = dayjs(dayjs(this.currentInfo.leaveTime).add(1, 'day').format('YYYY-MM-DD') + ' ' + '08:00:00').unix();
                        }
                    }
                }
                if (this.currentInfo.priceRecord) {
                    let a = JSON.parse(this.currentInfo.priceRecord);
                    var arr = [];
                    for (let key in a) {
                        //取键
                        let o = {};
                        o.time = key;
                        o.price = Number(a[key]);
                        arr.push(o);
                    }
                    this.sortKey(arr, 'time');
                    this.priceRecordList = arr;
                }
                if (this.currentInfo.dayIn && this.currentInfo.priceRecord) {
                    let a = JSON.parse(this.currentInfo.priceRecord);
                    var arr = [];
                    for (let key in a) {
                        //取键
                        let o = {};
                        o.time = key;
                        o.price = Number(a[key]);
                        arr.push(o);
                    }
                    this.sortKey(arr, 'time');
                    this.priceRecordListBackInfo = arr;
                    this.priceRecordListBackInfo.reverse().splice(0, this.currentInfo.dayIn);
                }
                this.currentInfo.order.payment > 0 ? '已支付' : '未支付';
                if (this.currentInfo.depositCut > 0) {
                    this.getDepositCutReason();
                }
                // 原价
                this.roomRealAmount = this.currentInfo.roomRealAmount ? this.currentInfo.roomRealAmount : 0;
                // 代金券
                this.couponAmount = this.currentInfo.couponAmount ? this.currentInfo.couponAmount : 0;
                // 连租折扣
                if (this.currentInfo.order.roomScaleDiscountId) {
                    this.getroomScaleDiscount(this.currentInfo.order.roomScaleDiscountId);
                } else {
                    this.discountInfo = 0;
                    if (this.currentInfo.order.memberDiscount) {
                        this.memberDiscount = this.roomRealAmount - this.discountInfo - Math.floor((this.roomRealAmount - this.discountInfo) * this.currentInfo.order.memberDiscount * 100) / 100;
                    } else {
                        this.memberDiscount = this.roomRealAmount - this.discountInfo - Math.floor((this.roomRealAmount - this.discountInfo) * 1 * 100) / 100;
                    }
                }
                if (this.currentInfo.hotel.faceApi == 0) {
                    this.isLock = false;
                } else {
                    if (this.currentInfo.isNoCard == 1) {
                        this.isLock = false;
                    } else {
                        this.isLock = true;
                    }
                }
                if (this.currentInfo.cooperOrder.phone) {
                    this.number = this.currentInfo.cooperOrder.phone;
                    if (this.number) {
                        this.Local(this.number);
                    }
                } else if (this.currentInfo.sysUser) {
                    this.number = this.currentInfo.sysUser.phone ? this.currentInfo.sysUser.phone : '';
                    if (this.number) {
                        this.Local(this.number);
                    }
                }
                // 延时退房
                if (this.currentInfo.isAutoDetain == 1) {
                    this.value = true;
                } else {
                    this.value = false;
                }
                // 免费延时至14:00
                if (this.currentInfo.order.endTime.substr(11, 8) == '14:00:00') {
                    this.delayValue = true;
                } else {
                    this.delayValue = false;
                }
                this.liverInfo();
            });
        },
        // 根据订单房间查询扣押记录
        getDepositCutReason() {
            GetBusinessDepositCutRecordByorderroom(this.currentInfo.id).done((res) => {
                this.cutRecordInfo = res.data;
            });
        },
        // 查询连住折扣
        getroomScaleDiscount(id) {
            GetBusinessRoomscaleDiscountInfo(id).done((res) => {
                let discountInfo = (this.roomRealAmount * res.data.discount) / 10;
                this.discountInfo = this.roomRealAmount - discountInfo;
                this.memberDiscount = this.roomRealAmount - this.discountInfo - Math.ceil((this.roomRealAmount - this.discountInfo) * this.currentInfo.order.memberDiscount * 100) / 100;
            });
        },
        // 展开实付房费
        priceshow() {
            this.turnOpen = !this.turnOpen;
        },
        // 手机归属地查询
        Local(number) {
            if (this.currentInfo.orderType != 3) {
                this.$jsonp(`https://tool.bitefu.net/shouji/?mobile=${number}`)
                    .then((popdata) => {
                        this.getLocalInfo = popdata;
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        },
        // 数据查询
        firstGo() {
            if (this.currentInfo.orderType == 3) {
                GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                    this.options = res.data.map((item) => {
                        return {
                            label: item.dictLabel,
                            value: item.dictValue
                        };
                    });
                    let payments = {};
                    for (var i = 0; i < this.options.length; i++) {
                        payments[this.options[i].value] = this.options[i].label;
                    }
                    this.payments = payments;
                });
            } else {
                GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                    let orderSourceMap = {};
                    res.data.forEach((item) => {
                        orderSourceMap[item.dictSort] = item.dictLabel;
                    });
                    this.orderSourceMap = orderSourceMap;
                });
            }
        },
        // 查询房间入住信息
        liverInfo() {
            GetBusinessOrderroomuserByorderroom(this.currentInfo.id).done((res) => {
                this.infoList = res.data;
                if (this.currentInfo.orderType == 3 || this.currentInfo.orderType == 4) {
                    this.getSigning();
                }
            });
        },
        // 人证对比
        toggle(isLock) {
            if (isLock == true) {
                this.person_box = true;
            }
        },
        // 人证确定
        person_ok() {
            this.person_box = false;
            PostBusinessOrderroomNocardOn(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                this.isLock = false;
            });
        },
        person_cancel() {
            this.person_box = false;
        },
        // 延时退房
        openedDelay() {
            let endT = dayjs(this.currentInfo.order.endTime).unix();
            let isEndT = dayjs(dayjs().format('YYYY-MM-DD HH:mm:ss')).unix();
            if (isEndT < endT) {
                if (this.value == true) {
                    MessageBox.confirm('', {
                        message: '确认关闭延时退房？',
                        title: '延时退房',
                        cancelButtonText: '取消',
                        confirmButtonText: '确定'
                    })
                        .then((action) => {
                            if (action == 'confirm') {
                                PostBusinessOrderroomAutoDetainOff(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                                    this.value = false;
                                });
                            }
                        })
                        .catch((e) => {
                            console.log(e);
                        });
                } else {
                    MessageBox.confirm('', {
                        message: '确认开启延时退房？',
                        title: '延时退房',
                        cancelButtonText: '取消',
                        confirmButtonText: '确定'
                    })
                        .then((action) => {
                            if (action == 'confirm') {
                                PostBusinessOrderroomAutoDetainOn(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                                    this.value = true;
                                });
                            }
                        })
                        .catch((e) => {
                            console.log(e);
                        });
                }
            } else if (isEndT > endT) {
                MessageBox.confirm('', {
                    message: '已开始计费，无法关闭延时退房，可以直接退房',
                    title: '提示',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                });
            }
        },
        // 是否开启延迟功能-至14:00
        openedDelayValue() {
            MessageBox.confirm('', {
                message: '开启后无法关闭',
                title: '确认开启免费延时退房？',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessOrderroomDelaySwitchOn(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                            this.delayValue = true;
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 删除入住人
        dele(id) {
            if (this.infoList.length == 1) {
                Toast({
                    message: '至少需要一个入住人',
                    duration: 900
                });
                return false;
            }
            this.id = id;
            this.edit_box = true;
        },
        // 查看入住人身份证
        lookIdCard(item) {
            this.$router.push({
                path: '/addPerson',
                query: {
                    realName: item.realName,
                    idenIds: item.idenIds
                }
            });
        },
        edit_ok() {
            PostBusinessOrderroomuserRemove(this.id).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 1000
                });
                this.liverInfo();
            });
            this.edit_box = false;
        },
        edit_cancel() {
            this.edit_box = false;
        },
        // 添加入住人
        addLiver() {
            this.$router.push({
                path: '/addPerson',
                query: {
                    orderRoomUserId: this.currentInfo.id
                }
            });
        },
        // 办理入住
        btnLive() {
            this.$router.push({
                name: 'DealLive',
                query: {
                    orderDetail: this.currentInfo
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(this.currentInfo));
        },
        // 换房(未办理入住）
        btnchange() {
            this.$router.push({
                path: '/fdchange',
                query: {
                    orderDetail: this.currentInfo
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(this.currentInfo));
        },
        // 修改订单
        btnamend() {
            this.$router.push({
                name: 'Amend',
                query: {
                    item_amend: this.currentInfo
                }
            });
            localStorage.setItem('item_amend', JSON.stringify(this.currentInfo));
        },
        // 取消订单
        btncancel() {
            this.sheetVisible = false;
            MessageBox.confirm('', {
                message: '取消订单后，房费和押金将退还顾客，请慎重操作',
                title: '取消订单',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessOrderroomCancel(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                            setTimeout('window.history.go(-1)', 500);
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 选择时间
        formatDate(date) {
            let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
            this.doble.push(doble);
            this.startTime = this.doble[0];
            this.endTime = this.doble[1];
            return doble;
        },
        onConfirm(date) {
            const [start, end] = date;
            this.showTime = false;
            this.showCoupon = true;
            this.doble = [];
            this.date = `${this.formatDate(start)} 至 ${this.formatDate(end)}`;
        },
        // 单选的change事件
        chooseChange(dictValue) {
            if (this.selectedData.indexOf(dictValue) > -1) {
                this.removeByValue(this.selectedData, dictValue);
            } else {
                this.selectedData.push(dictValue);
            }
            if (this.selectedData.length < this.ordertTypeAll.total) {
                this.AllChecked = true;
            } else {
                this.AllChecked = false;
            }
            this.orderType = this.selectedData.toString();
        },
        // 过滤
        removeByValue(arr, val) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i] == val) {
                    arr.splice(i, 1);
                    break;
                }
            }
        },
        // 使用范围-ordertype
        getOrderType(type, dictType) {
            GetSystemDictDataByType({ dictType: dictType }).done((res) => {
                if (type == 'type') {
                    this.ordertTypeAll = res.data;
                } else if (type == 'status') {
                    this.orderStatusAll = {};
                    res.data.forEach((item) => {
                        this.orderStatusAll[item.dictValue] = item.dictLabel;
                    });
                }
            });
        },
        // 发送优惠券
        sendCoupon() {
            if (!this.memName) {
                Toast({
                    message: '请输入面额',
                    duration: 900
                });
                return false;
            }
            if (!this.memNumber) {
                Toast({
                    message: '请输入数量',
                    duration: 900
                });
                return false;
            }
            if (!this.menPrice) {
                Toast({
                    message: '请输入满减金额',
                    duration: 900
                });
                return false;
            }
            if (!this.startTime) {
                Toast({
                    message: '请选择有效时间段',
                    duration: 900
                });
                return false;
            }
            if (!this.orderType) {
                Toast({
                    message: '请选择领取范围',
                    duration: 900
                });
                return false;
            }
            let params = {
                couponSource: '2',
                userId: this.currentInfo.userId,
                hotelId: this.currentInfo.hotelId,
                couponAmount: this.memName,
                fullAmount: this.menPrice,
                couponNum: this.memNumber,
                startTime: this.startTime,
                endTime: this.endTime,
                orderType: this.orderType
            };
            PostBusinessUserCouponAdd(params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            this.showCoupon = false;
        },
        // 订单重新下发密码
        btnAgain() {
            this.sheetVisible = false;
            PostBusinessOrderroomPasswordSend(this.currentInfo.id, { orderId: this.currentInfo.orderId }).done((res) => {
                MessageBox.alert('下发密码成功').then((action) => {
                    setTimeout('window.history.go(-1)', 500);
                });
            });
        },
        // 提前退房or退租
        btnLeaveroom() {
            this.sheetVisible = false;
            if (this.currentInfo.orderType == 3 || this.currentInfo.orderType == 4) {
                this.$router.push({
                    path: '/rentCount',
                    query: {
                        cooperOrderRoomId: this.currentInfo.id
                    }
                });
                localStorage.setItem('rentCount_info', JSON.stringify(this.currentInfo));
            } else {
                MessageBox.confirm('', {
                    message: '退房后，将不可恢复，请慎重操作',
                    title: '是否退房',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                })
                    .then((action) => {
                        if (action == 'confirm') {
                            let data = {
                                orderId: this.currentInfo.orderId,
                                orderRoomId: this.currentInfo.id
                            };
                            PostBusinessOrderroomLeave(data).done((res) => {
                                MessageBox.alert('退房成功').then((action) => {
                                    setTimeout('window.history.go(-1)', 500);
                                });
                            });
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        },
        // 单位订单房间
        CooperRooms(item) {
            this.$router.push({
                path: '/business-rooms',
                query: {
                    cooperOrderId: item.id
                }
            });
        },
        // 续租
        gorenew() {
            this.$router.push({
                path: '/renew',
                query: {
                    roomSn: this.currentInfo.roomSn,
                    scaleName: this.currentInfo.roomScale.scaleName,
                    cooperOrderId: this.currentInfo.cooperOrderId,
                    cooperOrderRoomId: this.$route.query.cooperOrderRoomId,
                    endTime: this.currentInfo.cooperOrder.endTime,
                    roomRealAmount: this.currentInfo.roomRealAmount,
                    orderType: this.currentInfo.orderType
                }
            });
        },
        // 房费优惠
        btnback(num) {
            var currentInfo = {
                item: this.currentInfo,
                num: num
            };
            this.$router.push({
                name: 'DealLive',
                query: currentInfo
            });
            localStorage.setItem('orderDetail', JSON.stringify(currentInfo));
        },
        // 扣押
        btndeposit() {
            this.$router.push({
                name: 'DealLive',
                query: this.currentInfo
            });
            localStorage.setItem('orderDetail', JSON.stringify(this.currentInfo));
        },
        // 关闭or打开
        isblock() {
            this.sheetVisible = false;
        },
        // 重新下发短信
        btnInfo() {
            this.sheetVisible = false;
            this.password_show = true;
            if (this.currentInfo.order) {
                this.pass_phone = this.currentInfo.order.phone ? this.currentInfo.order.phone : this.currentInfo.sysUser.phone;
            }
        },
        // 重新下发短信-取消
        pass_can() {
            this.password_show = false;
        },
        // 重新下发短信-确定
        pass_ok() {
            // 已验证191,181手机号
            if (!this.pass_phone || !/^1[3-9]\d{9}$/.test(this.pass_phone)) {
                Toast({
                    message: '请正确输入手机号',
                    duration: 900
                });
                this.pass_phone = '';
                return false;
            }
            let roomUser = this.currentInfo.sysUser == null ? this.currentInfo.order.roomUser : this.currentInfo.sysUser.realName;
            let params = {
                roomUser: roomUser,
                phone: this.pass_phone,
                orderId: this.currentInfo.orderId
            };
            PostBusinessOrderroomSendMessageAgain(this.currentInfo.id, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            this.password_show = false;
        },
        // 换表
        changeWatch() {
            this.$router.push({
                path: '/changeWatch',
                query: {
                    id: this.currentInfo.id,
                    orderId: this.currentInfo.orderId
                }
            });
        },
        // 电费充值
        eleCharge() {
            this.$router.push({
                path: '/eleManage',
                query: {
                    hotelName: this.currentInfo.hotel.hotelName,
                    scaleName: this.currentInfo.roomScale.scaleName,
                    virscaleName: this.currentInfo.virtualRoomScale.scaleName,
                    roomSn: this.currentInfo.roomSn,
                    orderRoomId: this.currentInfo.id,
                    orderId: this.currentInfo.orderId
                }
            });
        },
        // 月租房费账单
        btnRoomFee() {
            this.$router.push({
                path: '/roomFee',
                query: {
                    id: this.currentInfo.id,
                    orderId: this.currentInfo.orderId
                }
            });
        },
        // 月租生活账单
        btnLifeFee() {
            this.$router.push({
                path: '/lifeFee',
                query: {
                    id: this.currentInfo.id
                }
            });
        },
        // 退租
        btnBackRent() {
            this.$router.push('/backRent');
        },
        // 签约展开与折叠
        isSigning() {
            this.Signing = !this.Signing;
        },
        // 签约合同展开与折叠
        isRent(item) {
            item.setStatu = !item.setStatu;
        },
        // 根据订单房间查询合同
        getSigning() {
            GetBusinessRenewalContractByOrderRoom(this.currentInfo.id).done((res) => {
                let list = res.data.map((item) => {
                    this.$set(item, 'setStatu', false);
                    return item;
                });
                this.signingInfo = list;
            });
        },
        // cooper
        btnchange_cooper() {
            GetBusinessRoomFreeCooperByhotel({ hotelId: this.currentInfo.hotelId }).done((res) => {
                this.cooperChangeRooms = res.data;
                this.cooperRooms = true;
            });
        },
        choosedRoomcancle() {
            this.cooperRooms = false;
        },
        choosedRoom(item) {
            let params = {
                orderId: this.currentInfo.orderId,
                toScaleId: this.currentInfo.roomScaleId,
                toVirtualRoomScaleId: '0',
                toRoomId: item.id
            };
            PostBusinessOrderroomRoomchange(this.currentInfo.id, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(0)', 900);
            });
        },
        // 开票
        goRoute(link) {
            this.$router.push({
                path: link,
                query: {
                    orderRoomId: this.$route.query.orderRoomId,
                    invoiceAmount: this.currentInfo.invoiceAmount
                }
            });
        }
    }
};
</script>
