#range > .receive {
    display: flex;
    padding: 8px 16px;
    text-align: left;
    color: #000;
}
#range > .receive > label {
    width: 90px;
}
#range > .receive > ul {
    width: 180px;
}
#range > .receive > ul > li {
    height: 30px;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell__value {
    min-width: 66%;
    text-align: left;
}
.noborder {
    border: none;
}
.border_b2 {
    border-bottom: 1px solid #b2b2b2;
    opacity: 0.7;
    padding-bottom: 8px;
}
</style>



